* {
  box-sizing: border-box; }

.details-title {
  font-family: sans-serif;
  font-weight: 1000;
  font-size: 12vmin;
  margin-bottom: 3vmin;
  color: #202030; }
  @media screen and (min-width: 332px) {
    .details-title {
      font-size: 11vmin; } }
  @media screen and (min-width: 576px) {
    .details-title {
      font-size: 10vmin; } }
  @media screen and (min-width: 768px) {
    .details-title {
      font-size: 9vmin; } }
  @media screen and (min-width: 992px) {
    .details-title {
      font-size: 8vmin; } }
  @media screen and (min-width: 1200px) {
    .details-title {
      font-size: 7vmin; } }

.details-back-btn {
  background-color: #202030;
  color: whitesmoke;
  text-decoration: none;
  cursor: pointer;
  border: none;
  margin: auto auto 15vmin;
  border-radius: 2.6vmin;
  font-size: 5.2vmin;
  padding: 2.6vmin 5.2vmin;
  transition: all .2s ease-in-out; }
  @media screen and (min-width: 332px) {
    .details-back-btn {
      border-radius: 2.4vmin;
      font-size: 4.8vmin;
      padding: 2.4vmin 4.8vmin; } }
  @media screen and (min-width: 576px) {
    .details-back-btn {
      border-radius: 2.2vmin;
      font-size: 4.4vmin;
      padding: 2.2vmin 4.4vmin; } }
  @media screen and (min-width: 768px) {
    .details-back-btn {
      border-radius: 2.1vmin;
      font-size: 4.1vmin;
      padding: 2.1vmin 4.2vmin; } }
  @media screen and (min-width: 992px) {
    .details-back-btn {
      border-radius: 1.9vmin;
      font-size: 3.8vmin;
      padding: 1.9vmin 3.8vmin; } }
  @media screen and (min-width: 1200px) {
    .details-back-btn {
      border-radius: 1.8vmin;
      font-size: 3.6vmin;
      padding: 1.8vmin 3.6vmin; } }

.details-back-btn:hover {
  background-color: #2e2e45;
  cursor: pointer;
  transition: transform 0.7s;
  transform: scale(1.1); }

.details-back-btn:active {
  top: 2px; }

.details-body-container {
  margin: 0 auto;
  width: 600px; }
  @media screen and (min-width: 768px) {
    .details-body-container {
      width: 610px; } }
  @media screen and (min-width: 1200px) {
    .details-body-container {
      width: 1220px; } }

.details-logo-container > img {
  width: 600px; }
  @media screen and (min-width: 768px) {
    .details-logo-container > img {
      width: 300px; } }

.details-logo-container {
  display: inline-block;
  width: 600px;
  height: 600px; }
  @media screen and (min-width: 768px) {
    .details-logo-container {
      width: 300px;
      height: 300px; } }

.details-airplane-container > img {
  width: 600px; }
  @media screen and (min-width: 768px) {
    .details-airplane-container > img {
      width: 300px; } }

.details-airplane-container {
  display: inline-block;
  width: 600px;
  height: 600px; }
  @media screen and (min-width: 768px) {
    .details-airplane-container {
      width: 300px;
      height: 300px; } }

.details-info-container {
  display: inline-block;
  margin: 0 auto;
  font-size: 20px;
  width: 600px;
  height: 280px; }

.details-paragraph {
  height: 12.5%;
  display: grid;
  margin-bottom: 1%;
  grid-template-columns: 30% 70%; }

span {
  display: block;
  white-space: nowrap;
  text-overflow: ellipsis;
  border-radius: 5px;
  background-color: #e3e3e3;
  margin-left: 2%;
  padding-top: 4px;
  overflow: hidden; }

.details-logo-container, .details-airplane-container, .details-info-container {
  position: relative;
  right: -100vw; }

.translate-left {
  transition: transform 0.7s;
  transform: translate(-100vw, 0); }
