body {
  box-sizing: border-box;
  background-color: whitesmoke;
  text-align: center;
  font-family: sans-serif;
  font-size: 4vmin;
  color: #202030; }
  @media screen and (min-width: 576px) {
    body {
      font-size: 3.6vmin; } }
  @media screen and (min-width: 768px) {
    body {
      font-size: 3.2vmin; } }
  @media screen and (min-width: 992px) {
    body {
      font-size: 2.8vmin; } }
  @media screen and (min-width: 1200px) {
    body {
      font-size: 2.4vmin; } }

.display-none {
  display: none; }

.visibility-hidden {
  visibility: hidden; }

button:focus {
  box-shadow: none;
  outline: none; }

/* -----------------ANIMATION----------------------- */
.North {
  -ms-transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  -webkit-transition-duration: 1s;
  -webkit-animation-name: rotate-north;
  -webkit-animation-duration: 6s;
  -webkit-animation-iteration-count: 1; }

@-webkit-keyframes rotate-north {
  0%, 65%, 75% {
    -webkit-transform: rotate(0deg); }
  70% {
    -webkit-transform: rotate(360deg);
    -webkit-animation-timing-function: ease-in;
    -webkit-animation-delay: 10s; }
  100% {
    -webkit-transform: rotate(90deg); } }

.South {
  -ms-transform: rotate(270deg);
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
  -webkit-transition-duration: 1s;
  -webkit-animation-name: rotate-south;
  -webkit-animation-duration: 6s;
  -webkit-animation-iteration-count: 1; }

@-webkit-keyframes rotate-south {
  0%, 65%, 75% {
    -webkit-transform: rotate(0deg); }
  70% {
    -webkit-transform: rotate(360deg);
    -webkit-animation-timing-function: ease-in;
    -webkit-animation-delay: 10s; }
  100% {
    -webkit-transform: rotate(270deg); } }

.East {
  -ms-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  -webkit-transition-duration: 1s;
  -webkit-animation-name: rotate-east;
  -webkit-animation-duration: 6s;
  -webkit-animation-iteration-count: 1; }

@-webkit-keyframes rotate-east {
  0%, 65%, 75% {
    -webkit-transform: rotate(0deg); }
  70% {
    -webkit-transform: rotate(360deg);
    -webkit-animation-timing-function: ease-in;
    -webkit-animation-delay: 10s; }
  100% {
    -webkit-transform: rotate(180deg); } }

.West {
  -ms-transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
  -webkit-transition-duration: 1s;
  -webkit-animation-name: rotate-west;
  -webkit-animation-duration: 6s;
  -webkit-animation-iteration-count: 1; }

@-webkit-keyframes rotate-west {
  0%, 65%, 75% {
    -webkit-transform: rotate(0deg); }
  70% {
    -webkit-transform: rotate(360deg);
    -webkit-animation-timing-function: ease-in;
    -webkit-animation-delay: 10s; }
  100% {
    -webkit-transform: rotate(360deg); } }
